	module.exports = {"title":"Styling the map","path":"/map-sdk/overview/styling-map/","body":"<h1 id=\"style-the-map\"><a class=\"markdownIt-Anchor\" href=\"#style-the-map\"></a> Style the map</h1>\n<p>The maps SDK allows full customization when it comes to displaying your map. This means you can brand the map with the colors, icons, and fonts that match your apps UI. The customization can either be done using <a href=\"\">Mapbox Studio</a> or during runtime using the <a href=\"\">Runtime Styling APIs</a>.</p>\n<h3 id=\"changing-the-default-style\"><a class=\"markdownIt-Anchor\" href=\"#changing-the-default-style\"></a> Changing the default style</h3>\n<p>As powerful as styling the map can be, to get started using the Map SDK, it offers six professional styles that will look great in your app:</p>\n<ol>\n<li><strong>Mapbox Streets:</strong> Mapbox Streets is a comprehensive, general-purpose map that emphasizes legible styling of road and transit networks.</li>\n<li><strong>Outdoor:</strong> Mapbox Outdoors is a general-purpose map with curated datasets and specialized styling tailored to hiking, biking, and the most adventurous use cases.</li>\n<li><strong>Light and Dark:</strong> Mapbox Light and Mapbox Dark are subtle, full-featured maps designed to provide geographic context while highlighting the data on your analytics dashboard, data visualization, or data overlay.</li>\n<li><strong>Satellite:</strong> is our full global base map that is perfect as a blank canvas or an overlay for your own data.</li>\n<li><strong>Satellite Streets:</strong> combines our Mapbox Satellite with vector data from Mapbox Streets. The comprehensive set of road, label, and POI information brings clarity and context to the crisp detail in our high-resolution satellite imagery.</li>\n<li><strong>Traffic:</strong> Visually show realtime traffic using either the provided day or night traffic styles.</li>\n</ol>\n<h3 id=\"custom-mapbox-studio-style\"><a class=\"markdownIt-Anchor\" href=\"#custom-mapbox-studio-style\"></a> Custom Mapbox Studio style</h3>\n<p>To use a custom style, paste your style URL into your MapView’s mapbox:mapbox_styleUrl attribute in your activity’s layout file. If you would like to change the map style later on, call MapboxMap.setStyleUrl() with the new style and your map will update.</p>\n<!-- ### Using local JSON file -->\n","toc":"<ul class=\"txt-s inline-block mb12 bright-blue-color-on-hover-anchor\">\n<li>\n<ul>\n<li><a href=\"#changing-the-default-style\">Changing the default style</a></li>\n<li><a href=\"#custom-mapbox-studio-style\">Custom Mapbox Studio style</a></li>\n</ul>\n</li>\n</ul>\n"}

