	module.exports = {"title":"Mapbox Services","path":"/mapbox-services/overview/","body":"<p>Mapbox Java is an open source toolset for building applications that need navigation, directions, geocoding, static map imagery, etc. It conveniently wraps Mapbox APIs and builds off of them with tools which are useful for your applications. This SDK consist of two parts:</p>\n<ol>\n<li><strong>Mapbox-Java-Services:</strong> Has no dependency on the Android API and can be used in any of your Java projects.</li>\n<li><strong>Mapbox-Android-Services:</strong> Contains specific code for Android applications such as widgets and UI elements.</li>\n</ol>\n<h3 id=\"support-and-contributions\"><a class=\"markdownIt-Anchor\" href=\"#support-and-contributions\"></a> Support and contributions</h3>\n<ul>\n<li>If you are looking for support using this SDK, either reach out through <a href=\"https://stackoverflow.com/questions/tagged/mapbox+android\">Stack Overflow</a> or through our <a href=\"https://www.mapbox.com/contact/\">contact page</a>.</li>\n<li>If you have found a bug, and can provide steps to reliably reproduce it, open an issue in the /mapbox-java repository on GitHub and apply the bug label.</li>\n<li>If you have a feature request, open an issue in the /mapbox-java repository on GitHub, and apply the enhancement label.</li>\n<li>If you want to contribute, look over our contribution guidelines and open a pull request with your changes.</li>\n</ul>\n<h3 id=\"api-reference\"><a class=\"markdownIt-Anchor\" href=\"#api-reference\"></a> API reference</h3>\n<p>All public methods in all the project modules are well documented and even include a since tag so you can find when an API was first added. A link for all the module javadoc pages can be found in the list below:</p>\n<ul>\n<li><a href=\"https://www.mapbox.com/android-docs/api/mapbox-java/libjava-core/2.1.0/index.html\">mapbox-java-core</a></li>\n<li><a href=\"https://www.mapbox.com/android-docs/api/mapbox-java/libjava-geojson/2.1.0/index.html\">mapbox-java-geojson</a></li>\n<li><a href=\"https://www.mapbox.com/android-docs/api/mapbox-java/libjava-services/2.1.0/index.html\">mapbox-java-services</a></li>\n<li><a href=\"https://www.mapbox.com/android-docs/api/mapbox-java/libjava-services-rx/2.1.0/index.html\">mapbox-java-services-rx</a></li>\n<li><a href=\"https://www.mapbox.com/android-docs/api/mapbox-java/libandroid-services/2.1.0/index.html\">mapbox-android-services</a></li>\n<li><a href=\"https://www.mapbox.com/android-docs/api/mapbox-java/libandroid-telemetry/2.1.0/index.html\">mapbox-android-telemetry</a></li>\n<li><a href=\"https://www.mapbox.com/android-docs/api/mapbox-java/libandroid-ui/2.1.0/index.html\">mapbox-android-ui</a></li>\n</ul>\n<h3 id=\"access-tokens\"><a class=\"markdownIt-Anchor\" href=\"#access-tokens\"></a> Access tokens</h3>\n<p>If you plan to use any of our APIs such as directions, geocoding, navigation, etc. you’ll need to have a Mapbox access token which you’ll pass in as a parameter. An access token isn’t needed if you plan to use Mapbox Java only for GeoJSON parsing or Turf calculations. Read <a href=\"https://www.mapbox.com/help/create-api-access-token/\">this document</a> to learn more about access tokens.</p>\n<h3 id=\"position-and-point-objects\"><a class=\"markdownIt-Anchor\" href=\"#position-and-point-objects\"></a> Position and Point objects</h3>\n<p>Throughout the APIs exposed within Mapbox-Java, you’ll notice the Position object’s used heavily representing a coordinate. The order in which the coordinate pair are longitude followed by latitude. Creating a new Position is fairly straightforward and can be converted from a LatLng object if you are using our Maps SDK.</p>\n<p>The SDK will try to detect if your coordinates are outside the excepted range and log a warning. This range is between -180 to 180 for longitudes and -90 to 90 for latitude.</p>\n<blockquote>\n<p><strong>Note:</strong> if you happen to be using our Maps SDK with this SDK, you’ll notice that the LatLng object has the reverse order, latitude comes before the longitude value.</p>\n</blockquote>\n<p>Point will also be found in this SDK which is used specifically for GeoJSON objects. You can create a Point either directly from a Position object or passing in a longitude, latitude double array.</p>\n<pre class=\"hljs\"><code><span class=\"hljs-comment\">// Position representing The White House</span>\nPosition position = Position.fromCoordinates(-<span class=\"hljs-number\">77.03655</span>, <span class=\"hljs-number\">38.89770</span>);\n</code></pre>\n<h2 id=\"installation\"><a class=\"markdownIt-Anchor\" href=\"#installation\"></a> Installation</h2>\n<p>To start developing your application using Mapbox Java, you’ll need to first decide which installation method works best for you. The SDK is fully compatible with Android using Gradle and most of the project (besides the Android dependent modules) can also be included in a generic Java project using either Gradle or Maven. All dependencies given below can be found on MavenCentral.</p>\n<h3 id=\"gradle\"><a class=\"markdownIt-Anchor\" href=\"#gradle\"></a> Gradle</h3>\n<ol>\n<li>Start Android Studio</li>\n<li>Open up your applications <code>build.gradle</code></li>\n<li>Make sure your projects <code>minSdkVersion</code> is at API 15 or higher</li>\n<li>Under dependencies add a new build rule for the latest mapbox-android-services</li>\n<li>Click the Sync Project with Gradle Files near the toolbar in Studio</li>\n</ol>\n<blockquote>\n<p><strong>Note:</strong> If your application is close or exceeds the 65k method count limit, you can mitigate this problem by specifying only the specific Mapbox Android Service APIs. See the selectively compiling APIs section below.</p>\n</blockquote>\n<pre class=\"hljs\"><code>compile <span class=\"hljs-string\">'com.mapbox.mapboxsdk:mapbox-android-services:2.1.1'</span>\n</code></pre>\n<h3 id=\"maven\"><a class=\"markdownIt-Anchor\" href=\"#maven\"></a> Maven</h3>\n<p>If your project’s using Maven instead of Gradle, you can add the dependency inside your projects <code>POM.xml</code> file.</p>\n<pre class=\"hljs\"><code><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">dependency</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">groupId</span>&gt;</span>com.mapbox.mapboxsdk<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">groupId</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">artifactId</span>&gt;</span>mapbox-java-services<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">artifactId</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">version</span>&gt;</span>2.1.1<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">version</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">dependency</span>&gt;</span>\n</code></pre>\n<h3 id=\"selectively-compiling-apis\"><a class=\"markdownIt-Anchor\" href=\"#selectively-compiling-apis\"></a> Selectively compiling APIs</h3>\n<p>In earlier versions of Mapbox Java before to 2.0, you would have to compile the entire package of APIs. This in some cases, could cause Android applications to go over the 65,536 method count limit.</p>\n<p>Starting with 2.0, you now have the option to include either the entire package of APIs (using the dependencies listed above) or you can now selectively choose which specific APIs your application needs. For example, if you only need to handle GeoJSON serialization or deserialization inside your application you only need to include the GeoJSON dependency in your project.</p>\n<p>The list below shows all the current separated dependencies you can use in your Android application.</p>\n<pre class=\"hljs\"><code>compile <span class=\"hljs-string\">'com.mapbox.mapboxsdk:mapbox-java-core:2.1.1'</span>\ncompile <span class=\"hljs-string\">'com.mapbox.mapboxsdk:mapbox-java-geojson:2.1.1'</span>\ncompile <span class=\"hljs-string\">'com.mapbox.mapboxsdk:mapbox-java-services:2.1.1'</span>\ncompile <span class=\"hljs-string\">'com.mapbox.mapboxsdk:mapbox-java-services-rx:2.1.1'</span>\ncompile <span class=\"hljs-string\">'com.mapbox.mapboxsdk:mapbox-android-services:2.1.1'</span>\ncompile <span class=\"hljs-string\">'com.mapbox.mapboxsdk:mapbox-android-telemetry:2.1.1'</span>\ncompile <span class=\"hljs-string\">'com.mapbox.mapboxsdk:mapbox-android-ui:2.1.1'</span>\n</code></pre>\n<ul>\n<li>Mapbox Java core</li>\n<li>GeoJSON</li>\n<li>Mapbox Java Services</li>\n<li>Mapbox Java Services for RxJava projects</li>\n<li>Mapbox Android Services</li>\n<li>Telemetry</li>\n<li>Mapbox Android UI</li>\n</ul>\n<blockquote>\n<p><strong>Note:</strong> ProGuard directives are included in the Android dependencies to preserve the required classes.</p>\n</blockquote>\n","toc":"<ul class=\"txt-s inline-block mb12 bright-blue-color-on-hover-anchor\">\n<li>\n<ul>\n<li><a href=\"#support-and-contributions\">Support and contributions</a></li>\n<li><a href=\"#api-reference\">API reference</a></li>\n<li><a href=\"#access-tokens\">Access tokens</a></li>\n<li><a href=\"#position-and-point-objects\">Position and Point objects</a></li>\n</ul>\n</li>\n<li><a href=\"#installation\">Installation</a>\n<ul>\n<li><a href=\"#gradle\">Gradle</a></li>\n<li><a href=\"#maven\">Maven</a></li>\n<li><a href=\"#selectively-compiling-apis\">Selectively compiling APIs</a></li>\n</ul>\n</li>\n</ul>\n"}

