	module.exports = {"title":"Directions Matrix","path":"/mapbox-services/overview/directions-matrix/","body":"<h1 id=\"directions-matrix\"><a class=\"markdownIt-Anchor\" href=\"#directions-matrix\"></a> Directions Matrix</h1>\n<p>The Directions Matrix API returns all travel times between many points. The Matrix API will always return the duration on the fastest route. Durations between points may not be symmetric (for example A to B may have a different duration than B to A), as the routes may differ by direction due to one-way streets or turn restrictions. The Matrix API returns durations in seconds. It does not return route geometries or distances. Make sure to have a look at the API documentation if you want <a href=\"https://www.mapbox.com/api-documentation/#directions-matrix\">more information</a>.</p>\n<p>This API allows you to build tools that efficiently check the reachability of coordinates from each other, filter points by travel time, or run your own algorithms for solving optimization problems.</p>\n<p>The standard limit for request are a maximum 60 requests per minute and maximum 25 input coordinates. For example you can request a symmetric 25x25 matrix, an asymmetric 1x24 matrix with distinct coordinates or a 12x24 where sources and destinations share some coordinates. For higher volumes contact us.</p>\n<blockquote>\n<p><strong>Note:</strong> This replace the distance.v1 API which will be removed when <code>3.0.0</code> of Mapbox Services is released.</p>\n</blockquote>\n<h2 id=\"building-and-making-requests\"><a class=\"markdownIt-Anchor\" href=\"#building-and-making-requests\"></a> Building and making requests</h2>\n<p>Before requesting and receiving the Directions Matrix response, you’ll need to use the <code>MapboxDirectionsMatrix</code> builder to set the request parameters. The most basic request passes in a <code>List</code> of coordinates, sets the directions profile and provides an access token.</p>\n<pre class=\"hljs\"><code>MapboxDirectionsMatrix client = <span class=\"hljs-keyword\">new</span> MapboxDirectionsMatrix.Builder()\n  .setAccessToken(ACCESS_TOKEN)\n  .setProfile(DirectionsCriteria.PROFILE_WALKING)\n  .setCoordinates(positions)\n  .build();\n</code></pre>\n<h2 id=\"api-response\"><a class=\"markdownIt-Anchor\" href=\"#api-response\"></a> API response</h2>\n<p>Like all API calls inside Mapbox Services, the response will come inside a retrofit callback. inside the onResponse, you can access the APIs returned response if successful.</p>\n<pre class=\"hljs\"><code>client.enqueueCall(<span class=\"hljs-keyword\">new</span> Callback&lt;DirectionsMatrixResponse&gt;() {\n  <span class=\"hljs-meta\">@Override</span>\n  <span class=\"hljs-function\"><span class=\"hljs-keyword\">public</span> <span class=\"hljs-keyword\">void</span> <span class=\"hljs-title\">onResponse</span><span class=\"hljs-params\">(Call&lt;DirectionsMatrixResponse&gt; call, Response&lt;DirectionsMatrixResponse&gt; response)</span> </span>{\n\n  }\n\n  <span class=\"hljs-meta\">@Override</span>\n  <span class=\"hljs-function\"><span class=\"hljs-keyword\">public</span> <span class=\"hljs-keyword\">void</span> <span class=\"hljs-title\">onFailure</span><span class=\"hljs-params\">(Call&lt;DirectionsMatrixResponse&gt; call, Throwable t)</span> </span>{\n\n  }\n});\n</code></pre>\n","toc":"<ul class=\"txt-s inline-block mb12 bright-blue-color-on-hover-anchor\">\n<li><a href=\"#building-and-making-requests\">Building and making requests</a></li>\n<li><a href=\"#api-response\">API response</a></li>\n</ul>\n"}

