	module.exports = {"title":"Static Image","path":"/mapbox-services/overview/static-image/","body":"<h1 id=\"static-image\"><a class=\"markdownIt-Anchor\" href=\"#static-image\"></a> Static Image</h1>\n<p>The Mapbox Static API returns static maps and raster tiles from styles in the Mapbox Style Specification. Static maps are standalone images that can be displayed in an <code>ImageView</code> without the aid of a mapping library or API. They look like an embedded map without interactivity or controls. Specific to the Java API, it builds the request URL for you.</p>\n<h2 id=\"building-the-url\"><a class=\"markdownIt-Anchor\" href=\"#building-the-url\"></a> Building the URL</h2>\n<p>To begin with you’ll need to create a new instance of the <code>MapboxStaticImage</code> object and use it’s builder to customize your image request. The options offered in the builder include anything from setting the latitude and longitude to adding markers and other annotations. the image width and height are important parameters for displaying the correct aspect ratio inside your applications image view, and often, you will want to check if your users device supports retina or not by using <code>getResources().getDisplayMetrics().density</code>.</p>\n<pre class=\"hljs\"><code>MapboxStaticImage staticImage = <span class=\"hljs-keyword\">new</span> MapboxStaticImage.Builder()\n  .setAccessToken(getString(R.string.access_token))\n  .setStyleId(Constants.MAPBOX_STYLE_LIGHT)\n  .setLat(<span class=\"hljs-number\">45.4338</span>) <span class=\"hljs-comment\">// Image center Latitude</span>\n  .setLon(<span class=\"hljs-number\">12.3378</span>) <span class=\"hljs-comment\">// Image center longitude</span>\n  .setZoom(<span class=\"hljs-number\">13</span>)\n  .setWidth(<span class=\"hljs-number\">320</span>) <span class=\"hljs-comment\">// Image width</span>\n  .setHeight(<span class=\"hljs-number\">320</span>) <span class=\"hljs-comment\">// Image height</span>\n  .setRetina(<span class=\"hljs-keyword\">true</span>) <span class=\"hljs-comment\">// Retina 2x image will be returned</span>\n  .build();\n</code></pre>\n<h3 id=\"downloading-the-image\"><a class=\"markdownIt-Anchor\" href=\"#downloading-the-image\"></a> Downloading the image</h3>\n<p>After creating the <code>MapboxStaticImage</code> instance with all your customization parameters, you’ll need to handle downloading the image directly into your application so it is displayed properly to the user. The Mapbox Services SDK doesn’t offer any sort of image downloading/loading APIs but we highly recommend using a third party library such as <a href=\"http://square.github.io/picasso/\">Picasso</a> or <a href=\"https://github.com/bumptech/glide\">Glide</a> rather then an asynchronous task. Using Picasso, you can downloading the static map image with only a few lines of code.</p>\n<pre class=\"hljs\"><code>String imageUrl = staticImage.getUrl().toString();\nPicasso.with(<span class=\"hljs-keyword\">this</span>).load(imageUrl).into(imageView);\n</code></pre>\n<!-- ## Overlaying annotations\nAn overlay is data that can be applied on top of the map image at request time. these can be a mix of valid GeoJSON, a marker, a custom marker or path. Overlays cannot consist of more than 100 features. The maximum overlay length is 2083 characters.\n\nWhile building your request, you can choose to auto position the camera so that all your annotations fit within the viewport. To do this, remove the `setLat()` and `setLon()` and instead use `auto()`. `beforeLayer()` can be used to specify where in the map layer stack you'd like your annotations to appear. A marker overlay can be applied to the static map by providing a position, a name `pin-s`, `pin-m`, or `pin-l`, label and a color.\n\n```java\nMarker marker = new Marker()\n  .setName(\"pin-m\")\n  .setLabel(\"a\")\n  .setColor(\"50667f\")\n  .setLat(45.4338)\n  .setLon(12.3378);\n\nMapboxStaticImage staticImage = new MapboxStaticImage.Builder()\n  .setMarker(marker)\n  ...\n``` -->\n","toc":"<ul class=\"txt-s inline-block mb12 bright-blue-color-on-hover-anchor\">\n<li><a href=\"#building-the-url\">Building the URL</a>\n<ul>\n<li><a href=\"#downloading-the-image\">Downloading the image</a></li>\n</ul>\n</li>\n</ul>\n"}

