	module.exports = {"title":"Navigation Options","path":"/navigation/overview/navigation-options/","body":"<h1 id=\"navigation-options\"><a class=\"markdownIt-Anchor\" href=\"#navigation-options\"></a> Navigation Options</h1>\n<p>The set constants for navigation such as duration/distance an event must occur have been thoroughly tested, however, in some situations you might find yourself wanting to adjust these values to your liking. The <code>MapboxNavigtionOptions</code> object allows for tweaking of all the default values for alert levels, tolerance for offline, etc. The <code>MapboxNavigation</code> object constructor optionally takes in an instance of the <code>MapboxNavigtionOptions</code> object and uses the values set inside the options class until <code>MapboxNavigation</code>'s destroyed.</p>\n<pre class=\"hljs\"><code>MapboxNavigationOptions options = <span class=\"hljs-keyword\">new</span> MapboxNavigationOptions();\noptions.setManeuverZoneRadius(<span class=\"hljs-number\">70</span>);\n\nnavigation = <span class=\"hljs-keyword\">new</span> MapboxNavigation(<span class=\"hljs-keyword\">this</span>, Mapbox.getAccessToken(), options);\n</code></pre>\n<p>Below you’ll find information on <em>some</em> of the APIs exposed inside the options object that allows tweaking of the navigation behavior.</p>\n<h2 id=\"adjusting-step-completion-thresholds\"><a class=\"markdownIt-Anchor\" href=\"#adjusting-step-completion-thresholds\"></a> Adjusting step completion thresholds</h2>\n<p>Two heuristics are considered if the user has completed the current step and are now on the next step. The first value that’s adjustable is the <code>ManeuverZoneRadius</code> which the user must enter to count as completing a step. The value to set should be in unit meters. The other determining factor is the <code>MaxTurnCompletionOffset</code>. Adjusting this value to a smaller acceptable degree angle narrows the scope while increasing the value also increases the scope.</p>\n<h2 id=\"change-when-alert-levels-occur\"><a class=\"markdownIt-Anchor\" href=\"#change-when-alert-levels-occur\"></a> Change when alert levels occur</h2>\n<p>Another option available to tweak is when alert levels occur. To adjust the number of seconds left on step when a <code>MEDIUM_ALERT_LEVEL</code> alert occurs, use the <code>options.setMediumAlertInterval()</code>. High and Medium alert levels are only customizable, depart, arrival and low levels are all decided by other factors. When a step distance is short, such as when to maneuvers are close to each other, the minimum distance for an alert gets taken into account. use <code>options.setMinimumMediumAlertDistance()</code> to adjust this distance threshold.</p>\n<h2 id=\"off-route-threshold\"><a class=\"markdownIt-Anchor\" href=\"#off-route-threshold\"></a> Off-route threshold</h2>\n<p>Part of the off-route detection involves measuring the distance between the user’s exact location and the snapped to route location. If the distance is greater then the set <code>MaximumDistanceOffRoute</code>, the user is potentially considered off route. This threshold can also be adjusted using the <code>MapboxNavigationOptions</code> object.</p>\n<ol>\n<li>intransitive, now literary To exist; to have real existence.</li>\n<li>With there as dummy subject: to exist.</li>\n<li>intransitive To occupy a place.\nDefinitions by Grammarly</li>\n</ol>\n","toc":"<ul class=\"txt-s inline-block mb12 bright-blue-color-on-hover-anchor\">\n<li><a href=\"#adjusting-step-completion-thresholds\">Adjusting step completion thresholds</a></li>\n<li><a href=\"#change-when-alert-levels-occur\">Change when alert levels occur</a></li>\n<li><a href=\"#off-route-threshold\">Off-route threshold</a></li>\n</ul>\n"}

