	module.exports = {"title":"Mapbox Plugins","path":"/plugins/overview/","body":"<h1 id=\"mapbox-plugins\"><a class=\"markdownIt-Anchor\" href=\"#mapbox-plugins\"></a> Mapbox Plugins</h1>\n<p>Mapbox Plugins build on top of the <a href=\"/map-sdk/latest/getting-started/\">Map SDK</a> providing extra features in lightweight dependencies. Whether your looking to include traffic on top of your map or show the user location as a layer, plugins offer customizable APIs with a few lines of code to get started. Separating features into different plugins also allows for a more aggressive updating timeline.</p>\n<blockquote>\n<p><strong>Note:</strong> The Map SDK is required to use any of these plugins and many require passing in both the <code>MapView</code> and <code>MapboxMap</code> objects.</p>\n</blockquote>\n<h3 id=\"support-and-contributions\"><a class=\"markdownIt-Anchor\" href=\"#support-and-contributions\"></a> Support and contributions</h3>\n<ul>\n<li>Reach out through <a href=\"https://stackoverflow.com/questions/tagged/mapbox+android\">Stack Overflow</a> or through <a href=\"https://www.mapbox.com/contact/\">the Mapbox contact page</a> if you are looking for support with using this SDK.</li>\n<li>If you have found a bug and can provide steps to reliably reproduce it, open an issue in the <a href=\"https://github.com/mapbox/mapbox-plugins-android/issues\">/mapbox-plugins-android repository on GitHub</a>. Make sure to apply the bug label to your issue as well.</li>\n<li>If you have a feature request, open an issue in the /mapbox-plugins-android repository on GitHub and apply the feature label.</li>\n</ul>\n<!--- If you want to contribute to this SDK, please read [our contribution guidelines](https://github.com/mapbox/mapbox-gl-native/blob/master/CONTRIBUTING.md) and then open a pull request with your changes.-->\n<h3 id=\"api-reference\"><a class=\"markdownIt-Anchor\" href=\"#api-reference\"></a> API reference</h3>\n<p>All public methods and dependencies in the Mapbox Plugins project are well documented and can be either viewed inside of the source code or directly on the Javadoc (linked below). If you are using an older version of the SDK, you can still access the Javadoc by replacing the URL’s version number with the one that you are using inside of your application.</p>\n<!-- TODO -->\n <!-- - [0.1.0](https://www.mapbox.com/android-docs/api/map-sdk/5.0.2/index.html)-->\n<h2 id=\"installation\"><a class=\"markdownIt-Anchor\" href=\"#installation\"></a> Installation</h2>\n<p>Several steps must be taken before you start developing your application with the Maps SDK. Note that while we show how to add the stable version of the SDK inside your project, you can also use the nightly build/snapshot or the beta version, if one is available. The dependency given below can be found on MavenCentral.</p>\n<ol>\n<li>Start Android Studio</li>\n<li>Open up your application’s <code>build.gradle</code></li>\n<li>Make sure that your project’s <code>minSdkVersion</code> is at API 15 or higher</li>\n<li>Under dependencies, add a new build rule for the latest plugin you’d like to include</li>\n<li>Click the Sync Project with Gradle Files near the toolbar in Studio.</li>\n</ol>\n<!-- > **Note:** If your application is close or exceeds the 65k method count limit, you can mitigate this problem by enabling ProGuard inside your application. ProGuard directives are included in the Android dependencies to preserve the required classes. -->\n<pre class=\"hljs\"><code>repositories {\n  mavenCentral()\n}\n\ndependencies {\n  compile <span class=\"hljs-string\">'com.mapbox.mapboxsdk:mapbox-android-plugin-traffic:0.2.0'</span>\n}\n</code></pre>\n<h3 id=\"selectively-compiling-plugins\"><a class=\"markdownIt-Anchor\" href=\"#selectively-compiling-plugins\"></a> Selectively compiling plugins</h3>\n<p>Plugins give you the flexibility to only include the features your specific application requires. This means you can selectively choose which specific APIs your application needs. For example, if you only want to display a traffic layer inside your application you only need to include the Traffic Plugin dependency in your project.</p>\n<p>The list below shows all the current separated dependencies you can use in your Android application.</p>\n<pre class=\"hljs\"><code>compile <span class=\"hljs-string\">'com.mapbox.mapboxsdk:mapbox-android-plugin-traffic:0.2.0'</span>\n</code></pre>\n<ul>\n<li>Traffic Plugin</li>\n</ul>\n<!-- > **Note:** ProGuard directives are included in the Android dependencies to preserve the required classes.-->\n","toc":"<ul class=\"txt-s inline-block mb12 bright-blue-color-on-hover-anchor\">\n<li>\n<ul>\n<li><a href=\"#support-and-contributions\">Support and contributions</a></li>\n<li><a href=\"#api-reference\">API reference</a></li>\n</ul>\n</li>\n<li><a href=\"#installation\">Installation</a>\n<ul>\n<li><a href=\"#selectively-compiling-plugins\">Selectively compiling plugins</a></li>\n</ul>\n</li>\n</ul>\n"}

